.pricing1Icon {
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.imageWrapper {
  width: 80px;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.startup {
  position: relative;
  line-height: 180%;
  font-weight: 600;
}
.startingAt {
  position: relative;
  font-size: var(--font-size-sm);
  line-height: 100%;
  color: var(--color-darkslategray);
  text-align: center;
}
.name {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.mo {
  position: relative;
  line-height: 100%;
}
.pricing {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-darkslategray);
}
.loremIpsumDolor {
  flex: 1;
  position: relative;
  line-height: 140%;
  font-weight: 500;
}
.description {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--font-size-base);
}
.talkToAn {
  position: relative;
  letter-spacing: 2px;
  line-height: 140%;
  font-weight: 500;
}
.button,
.intro {
  align-self: stretch;
  display: flex;
  align-items: center;
}
.button {
  background-color: var(--color-white);
  border: 1px solid var(--color-gray);
  flex-direction: row;
  padding: var(--padding-base) var(--padding-5xl);
  justify-content: center;
  font-size: var(--font-size-xs);
  color: var(--color-black);
}
.intro {
  flex-direction: column;
  padding: 0 var(--padding-5xl) var(--padding-13xl);
  justify-content: flex-start;
  gap: var(--gap-base);
}
.separator {
  align-self: stretch;
  background-color: var(--color-whitesmoke);
  height: 1px;
}
.vectorIcon {
  position: relative;
  width: 20px;
  height: 20px;
}
.icon {
  width: 24px;
  height: 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.featureItem {
  flex: 1;
  position: relative;
  line-height: 150%;
}
.column,
.features,
.pricingFeature {
  display: flex;
  justify-content: flex-start;
}
.pricingFeature {
  align-self: stretch;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-5xs);
}
.column,
.features {
  flex-direction: column;
}
.features {
  align-self: stretch;
  padding: 0 var(--padding-5xl);
  align-items: center;
  gap: var(--gap-base);
  font-size: var(--font-size-sm);
  color: var(--color-darkslategray);
}
.column {
  background-color: var(--color-white);
  box-shadow: 0 4px 130px rgba(150, 163, 181, 0.12);
  width: 260px;
  padding: var(--padding-13xl) 0;
  box-sizing: border-box;
  align-items: flex-start;
  gap: var(--gap-5xl);
}
.button1 {
  align-self: stretch;
  background-color: var(--color-gray);
  border: 1px solid var(--color-gray);
  display: flex;
  flex-direction: row;
  padding: var(--padding-base) var(--padding-5xl);
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-xs);
  color: var(--color-white);
}
.column1,
.features1,
.pricingFeature6 {
  display: flex;
  justify-content: flex-start;
}
.pricingFeature6 {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-5xs);
}
.column1,
.features1 {
  flex-direction: column;
}
.features1 {
  align-self: stretch;
  padding: 0 var(--padding-base);
  align-items: center;
  gap: var(--gap-base);
  font-size: var(--font-size-sm);
  color: var(--color-darkslategray);
}
.column1 {
  background-color: var(--color-white);
  box-shadow: 0 4px 130px rgba(150, 163, 181, 0.3);
  width: 260px;
  padding: var(--padding-13xl) 0;
  box-sizing: border-box;
  align-items: flex-start;
  gap: var(--gap-5xl);
}
.columns,
.pricingComparison {
  width: 960px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.pricingComparison {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  overflow: hidden;
  padding: 64px var(--padding-5xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--color-gray);
  font-family: var(--font-inter);
}
@media screen and (max-width: 840px) {
  .columns {
    flex-direction: column;
  }
}
